$primaryColor: #4527a4;

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

@for $i from 1 through 12 {
	.columns-#{$i} {
		grid-template-columns: repeat(#{$i}, 1fr);
	}
}

@media (max-width: 768px) {
	@for $i from 1 through 12 {
		.columns-tablet-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
		}
	}
}

@media (max-width: 576px) {
	@for $i from 1 through 12 {
		.columns-mobile-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
		}
	}
}

// Counters
.wp-block-ctrb-counters .ctrbCounters {
	display: grid;

	* {
		box-sizing: border-box;
	}

	>div {
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.ctrbCounter {
		display: flex;
		flex-direction: column;
		justify-content: center;

		.counterIcon {
			margin-bottom: 5px;

			i {
				font-style: inherit;
			}
		}

		.counterNumber {
			margin: 0;
			margin-bottom: 10px;
		}

		.counterLabel {
			margin: 0;
		}

		.counterIconLabel {
			display: inline-flex;
			align-items: center;
			justify-content: center;

			i {
				font-style: inherit;
				margin-right: 10px;
			}
		}
	}
}